.reviews {
  position: relative;
  width: 672px;
  margin: auto auto 100px;

  @include vp-1199 {
    width: 678px;
    margin-bottom: 80px;
  }

  @include vp-767 {
    width: 290px;
    margin-bottom: 50px;
  }
}

.reviews h2 {
  margin-bottom: 30px;

  @include vp-1199 {
    font-size: 42px;
    line-height: 42px;
    margin-bottom: 25px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 26px;
  }
}

.reviews__wrap {
  width: 600px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;

  @include vp-1199 {
    width: 606px;
  }

  @include vp-767 {
    width: 266px;
  }
}

.reviews__list {
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.reviews__item {
  width: 560px;
  margin-right: 40px;
  display: flex;
  flex-shrink: 0;
  padding: 53px 58px 56px 62px;
  background: $dark-blue;
  box-sizing: border-box;

  @include vp-1199 {
    width: 566px;
    padding: 53px 62px 56px 65px;
  }

  @include vp-767 {
    width: 226px;
    padding: 47px 25px 36px 25px;
    position: relative;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  }
}

.reviews__item img {
  width: 80px;
  max-width: fit-content;
  height: 80px;
  border-radius: 50%;
  flex-shrink: 0;
  margin-top: 9px;
  margin-right: 29px;
  background: $dark-blue;

  @include vp-767 {
    width: 68px;
    height: 68px;
    position: absolute;
    top: 31px;
    left: 41px;
  }
}

.reviews__item h3 {
  font-size: 20px;
  line-height: 30px;
  font-weight: 700;
  color: $white;
  margin-top: 0;
  margin-bottom: 11px;

  @include vp-767 {
    padding-left: 99px;
    line-height: 26px;
    margin-bottom: 24px;
  }
}

.reviews__item p {
  font-size: 16px;
  line-height: 23px;
  font-weight: 400;
  color: $white;
  margin-top: 0;
  margin-bottom: 0;

  @include vp-767 {
    margin-bottom: 0;
    text-align: center;
    line-height: 22px;
  }
}

.reviews__control-left {
  border-color: transparent;
  position: absolute;
  border-left: 0;
  top: 210px;
  left: 0;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-right: 16px solid #afafaf;
  border-bottom: 16px solid transparent;

  @include vp-1199 {
    top: 192px;
  }

  @include vp-767 {
    top: 236px;
    border-top: 9px solid transparent;
    border-right: 10px solid #afafaf;
    border-bottom: 9px solid transparent;
  }
}

.reviews__control-left:hover,
.reviews__control-left:focus,
.reviews__control-right:hover,
.reviews__control-right:focus {
  cursor: pointer;
}

.reviews__control-left::before {
  content: "";
  position: absolute;
  top: -16px;
  left: 1px;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-right: 16px solid $white;
  border-bottom: 16px solid transparent;

  @include vp-767 {
    top: -9px;
    left: 1px;
    border-top: 9px solid transparent;
    border-right: 10px solid $white;
    border-bottom: 9px solid transparent;
  }
}

.reviews__control-right {
  border-color: transparent;
  position: absolute;
  top: 210px;
  right: 0;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-left: 16px solid #afafaf;
  border-bottom: 16px solid transparent;

  @include vp-1199 {
    top: 192px;
  }

  @include vp-767 {
    top: 236px;
    border-top: 9px solid transparent;
    border-left: 10px solid #afafaf;
    border-bottom: 9px solid transparent;
  }
}

.reviews__control-right::before {
  content: "";
  position: absolute;
  top: -16px;
  right: 1px;
  width: 0;
  height: 0;
  border-top: 16px solid transparent;
  border-left: 16px solid $white;
  border-bottom: 16px solid transparent;

  @include vp-767 {
    top: -9px;
    right: 1px;
    border-top: 9px solid transparent;
    border-left: 10px solid $white;
    border-bottom: 9px solid transparent;
  }
}

.reviews__control-left:focus {
  outline: none;
}

.reviews__control-right:focus {
  outline: none;
}
